<template>
    <v-dialog v-model="dialog" hide-overlay persistent width="300">
        <v-card color="white" dark class="pt-3">
            <v-card-text class="text-center" style="color: #5e72e4;">
                Please wait . . .
                <v-progress-linear
                    indeterminate
                    color="#5e72e4"
                    class="mb-0"
                ></v-progress-linear>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "LoadingDialog",

    computed: {
        ...mapGetters({
            dialog: "getLoadingStatus",

        }),
    },

    methods: {
        ...mapActions({
            setDialogStatus: "updateLoadingDialogStatus"
        })
    },
};
</script>

